<template>
<div class="card">
	<div class="card-content">
		<div class="row-flex">
			<img v-bind:src="'data:image/jpeg;base64,'+product.image" />
		</div>
		<div class="row-flex" style="text-align: center">
			<strong>{{product.title}}</strong>
		</div>
		<div class="row-flex" style="min-height: 40px;">
			{{product.description}}
		</div>
		<div class="row-flex m-t-15">
			<button type="button" class="btn btn-success" v-on:click='onBuyProduct()'  style="width: 100%;">
				<small>Купить за {{product.cost}} Руб.</small>
			</button>
		</div>
	</div>
</div>
</template>
<script>
	import swal from 'sweetalert2'
	export default {
		components: {
			swal
		},
		props: {
			product: {
				type: Object,
				default: () => {}
			}
		},
		data () {
			return {
			}
		},
		methods: {
			onBuyProduct () {
				var self = this
				swal({
					title: 'Вы уверены?',
					text: 'Ваш продукт "' + this.product.title + '"',
					type: 'warning',
					showCancelButton: true,
					confirmButtonText: 'Да',
					cancelButtonText: 'Нет'
				}).then((result) => {
					if (result) {
						self.buyingProduct()
					} else {
						swal.close()
					}
				}).catch(swal.noop)
			},
			buyingProduct () {
				var self = this
				swal({
					position: 'top-right',
					title: 'Получаем данные...',
					showConfirmButton: false,
					onOpen: () => {
					swal.showLoading()
					}
				}).catch(swal.noop)
				this.$store.dispatch('shopBuyProduct', {
					data: {
						id: self.product.id
					},
					callback: function (data) {
						swal({
							title: 'Успешно',
							text: data.item,
							type: 'success',
							confirmButtonText: 'Закрыть'
						}).catch(swal.noop)
					},
					successFalse: function (data) {
						var error = (data.message === 'Failed to fetch')
							? 'Невозможно сделать запрос, нет доступа к серверу или интернету' : data.message
						swal({
							title: 'Ошибка',
							text: error,
							type: 'error',
							showConfirmButton: true
						})
					}
				})
			}
		},
		watch: {
		},
		mounted () {
		}
	}
</script>
<style>
</style>



