<template>
<div>
	<div class="row">
		<div class="col-lg-12 col-md-12">
			<div class="row-flex" v-for="(g, gIndex) in productGroupedItems">
				<div :class="'col-lg-' + colClassCount + ' col-md-' + colClassCount * 2" v-for='(item, index) in g'>
					<product-card :product="item"></product-card>
				</div>
			</div>
		</div>
	</div>
</div>
</template>
<script>
	import swal from 'sweetalert2'
	import productCard from './components/productCard'

	
	// import api from '@/api'
	export default {
		name: 'shop-index',
		components: {
			productCard
		},
		data () {
			return {
				productItems: [],
				productCountInRow: 4
			}
		},
		methods: {
			onGetProductItems () {
				var self = this
				this.$store.dispatch('shopGetProductItems', {
					data: {},
					callback: function (data) {
						self.productItems = data.items
						swal.close()
					},
					successFalse: function (data) {
						var error = (data.message === 'Failed to fetch')
							? 'Невозможно сделать запрос, нет доступа к серверу или интернету' : data.message
						swal({
							title: 'Ошибка',
							text: error,
							type: 'error',
							showConfirmButton: true
						})
					}
				})
			}
		},
		computed: {
			productGroupedItems () {
				var newArr = []
				var arr = this.productItems
				var size = this.productCountInRow
				for (var i = 0; i < arr.length; i += size) {
					newArr.push(arr.slice(i, i + size))
				}
				return newArr
			},
			colClassCount () {
				return 12 / this.productCountInRow
			}
		},
		watch: {
		},
		created () {
			this.onGetProductItems()
		},
		mounted () {
		}
	}
</script>
<style>
</style>



